
.inline-input{
    width: 45px;
    text-align: center;
    border:1px solid #f6f6f6;
    margin: 0 5px;
    padding: 3px;
}
.flexbox{
    font-size: 14px;
    padding: 5px 10px;
    height:72px;
}
.c-left{
    float: left;
}
.c-top,.c-center,.c-bottom{
    height:24px;
    line-height: 24px;
}
.c-right{
    float: right;
    height:50px;
    margin-top: 11px;
    width: 120px;
    text-align: center;
}
.c-clear{
    clear:both
}
.upBtn{
    height:25px;
    width: 50px;
    background-color: brown;
    color:#fff;
    text-align: center;
    font-size:14px;
    line-height: 25px; 
    display: inline-block;
}
.lastTime{
    height:25px;
    line-height: 14px;
    font-size: 14px;
    margin-top: 5px;
}



